import React, { useState, createContext, useEffect } from "react";
export const TimeContext = createContext("");

export function TimeProvider({ children }: any) {
  const [time, setTime] = useState(new Date());
  const locale: string = "en";
  const day = time.toLocaleDateString(locale, { weekday: "long" });
  const date = `${day}, ${time.getDate()} ${time.toLocaleDateString(locale, {
    month: "long",
  })}\n\n`;
  const hour = time.getHours();

  const wish = `Good ${(hour < 12 && "Morning") || (hour < 17 && "Afternoon") || "Evening"}`;
  
  const displayTime: any = time.toLocaleTimeString(locale, {
    hour: "numeric",
    hour12: false,
    minute: "numeric",
    second: `2-digit`,
  });

  useEffect(() => {
    const timerId = setInterval(() => {
      setTime(new Date());
    }, 1000);
    return () => clearInterval(timerId);
  }, []);

  return (
    <TimeContext.Provider value={`${wish}. Time is: ${displayTime} ${date}`}>
      {children}
    </TimeContext.Provider>
  );
}



